.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.header .logo {
  display: flex;
}

.header .nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.header .nav__list .nav__item {
  margin-right: 20px;
  position: relative;
}

.header .nav__list .nav__item:last-child {
  margin-right: 0;
}

.header .nav__list .nav__item .nav__link {
  color: #060b35;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
  position: relative;
}

.header .nav__list .nav__item .nav__link:hover, .header .nav__list .nav__item .is-active {
  color: #00acdc;
}

.header .nav__list .nav__item .is-active:after {
  content: "";
  background-color: #00acdc;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  width: 200px;
  padding: 32px 16px 16px;
  transition: background-color .3s, color .3s, border .3s, scale .3s;
}

.card:hover {
  scale: 1.2;
}

.card__img {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__info {
  color: #060b35;
  padding-bottom: 4px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__productCode {
  color: #616070;
  padding-bottom: 16px;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__buyButton {
  color: #fff;
  text-align: center;
  cursor: pointer;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  width: 166px;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

.card__buyButton:hover {
  transition-property: background-color, color, border;
  transition-duration: inherit;
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  padding-top: 24px;
  padding-bottom: 16px;
  display: flex;
}

.card__price_label {
  color: #616070;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price_amount {
  color: #060b35;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__review-container {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.card__review-container .stars {
  align-items: center;
  display: flex;
}

.card__review-container .stars--4 {
  display: flex;
}

.card__review-container .stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.card__review-container .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.card__review-container .card__review-text {
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.catalog {
  grid-template-columns: repeat(1, 2px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .catalog {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .catalog {
    grid-template-columns: repeat(4, 200px);
  }
}

body, p {
  margin: 0;
}

html {
  font-family: Roboto, sans-serif;
}
